import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

 
jcTypeForm: FormGroup;
jctypeId = 0;  
ajaxData : TypeData[];
jcType= new TypeData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.jctypeId).subscribe(params=>{
			if(params.jctypeId != undefined){
				this.jctypeId=params.jctypeId; 
			}
		}); 
//	console.log("jctypeId=="+this.jctypeId);
	if(this.jctypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.jcTypeForm = new FormGroup({
			'jctypeId' : new FormControl(''),
			'jctypeName' : new FormControl('', [ Validators.required]),
			'jctypeEntryId' : new FormControl(''),
			'jctypeEntryDate' : new FormControl(''),
			'jctypeModifiedId' : new FormControl(''),
			'jctypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get jctypeName(){ return this.jcTypeForm.get('jctypeName')};
	    get jctypeEntryId(){ return this.jcTypeForm.get('jctypeEntryId')};
	    get jctypeEntryDate(){ return this.jcTypeForm.get('jctypeEntryDate')};
	    get jctypeModifiedId(){ return this.jcTypeForm.get('jctypeModifiedId')};
	    get jctypeModifiedDate(){ return this.jcTypeForm.get('jctypeModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<TypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jctype-data ',
							 			JSON.stringify({ 'jctypeId' :this.jctypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcType = data['populateData'][0];
																		console.log("jctypeModifiedId=="+this.jcType.jctypeModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/jc-type-list']);     
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcType[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.jctypeId == 0){ 
				this.jcType['requestType'] = 'add';
			}else{
				this.jcType['requestType'] = 'update';
				this.jcType['jctypeId'] = this.jctypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jctype',
									JSON.stringify(this.jcType),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcTypeForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcType);
	  }
 
  onDelete(){
		 this.jcType['requestType'] = 'delete';
		this.jcType['jctypeId'] = this.jctypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jctype',
									JSON.stringify(this.jcType),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcTypeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  TypeData {
    jctypeId: string;
	jctypeName: string;
	jctypeEntryId: string;
	jctypeEntryBy: string;
	jctypeEntryDate: string;
	jctypeModifiedId: string;
	jctypeModifiedBy: string;
	jctypeModifiedDate: string;
  
}


